html,
body {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 100%;
}

body {
  position: relative;
  z-index: 1;
}

@media only screen and (min-width: 30em) {
  body {
    z-index: 2;
  }
}

@media only screen and (min-width: 47.875em) {
  body {
    z-index: 3;
  }
}

@media only screen and (min-width: 62em) {
  body {
    z-index: 4;
  }
}

@media only screen and (min-width: 75em) {
  body {
    z-index: 5;
  }
}

a,
button {
  transition: all 300ms cubic-bezier(0.19, 1, 0.22, 1);
}

a:hover,
button:hover {
  transition: all 300ms cubic-bezier(0.19, 1, 0.22, 1);
  text-decoration: none;
}

/* IE10 viewport hack for Surface/desktop Windows 8 bug
 * http://getbootstrap.com/getting-started/#support-ie10-width
 */
@-webkit-viewport {
  width: device-width;
}

@-moz-viewport {
  width: device-width;
}

@-ms-viewport {
  width: device-width;
}

@-o-viewport {
  width: device-width;
}

@viewport {
  width: device-width;
}

.navbar {
  margin-bottom: 0;
}

.flex-container {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 62em) {
  .flex-container {
    flex-direction: column;
    flex-wrap: wrap;
  }
}

.flex-item {
  padding-left: 1em;
  padding-left: 1em;
}

.flex {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
}

.center-v {
  height: 100%;
}

/* -------------------------------------------
//  Actions
// -------------------------------------------*/
a {
  text-decoration: none;
}

a:not(.btn):hover {
  text-decoration: underline;
}

.cta-banner .banner-title {
  font-size: 1.125rem;
  margin: 0;
  font-weight: normal;
}

.cta-banner .banner-phone {
  font-size: 1.25rem;
  font-weight: bold;
}

.cta-banner .flex-container {
  margin: 0;
}

.cta-banner .flex-item {
  padding-left: 1em;
  padding-left: 1em;
}

.cta-banner p {
  margin-bottom: 0;
}

.cta-banner,
.header-cta-banner {
  display: flex;
  justify-content: center;
  padding: 2em;
}

.cta-banner__container {
  display: flex;
  align-items: center;
  margin: 0;
}

@media only screen and (max-width: 23.44em) {
  .cta-banner__container {
    justify-content: center;
  }
}

.cta-banner__telgroup {
  display: flex;
  align-items: center;
  margin: 0 auto;
}

@media only screen and (max-width: 47.875em) {
  .cta-banner__telgroup {
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    text-align: center;
  }
  .cta-banner__telgroup .flex-item {
    padding-left: 0;
  }
}

.btn {
  padding: 0.375em 2em;
}

.btn:not(.btn-link) {
  font-weight: bold;
}

.btn-primary[disabled],
.btn-primary[disabled]:hover {
  background-color: #9f9c9d;
  color: #FFF;
  border: 0;
}

.btn-primary[disabled]:hover {
  text-decoration: underline;
}

.btn-icon {
  margin-right: 0.375em;
}

.acdc_provider_phone_no {
  color: #000;
  font-weight: bold;
}

@media only screen and (min-width: 47.875em) {
  .acdc_provider_phone_no {
    cursor: text;
  }
}

/* -------------------------------------------
//  Header
// -------------------------------------------*/
.site-header {
  width: 100%;
  position: relative;
  display: block;
}

.site-header .container,
.site-header .container-fluid {
  margin-bottom: 0;
  padding-top: 0;
  padding-bottom: 0;
}

.site-header .navbar-toggle {
  font-size: 1.375rem;
}

.site-header .navbar-toggle .icon-bar {
  height: 4px;
}

.site-header .navbar-extras a {
  display: inline-block;
  padding: 1em;
}

/*=========================================
=            Header CTA Banner            =
=========================================*/
#header-banner {
  height: 0;
}

.header-cta-banner {
  z-index: 100;
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 0.75em;
}

@media only screen and (max-width: 47.875em) {
  .header-cta-banner {
    padding-left: 0;
    padding-right: 0;
  }
}

.header-cta-banner .banner-title {
  font-size: 1rem;
  margin: 0;
}

.header-cta-banner .cta-banner__telgroup {
  margin-left: auto;
  margin-right: 0;
}

@media only screen and (max-width: 47.875em) {
  .header-cta-banner .cta-banner__telgroup {
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
  }
}

.header-cta-banner.affix {
  font-weight: normal;
  box-shadow: 0px 2px 4px 1px rgba(0, 0, 0, 0.11), 0px 1px 2px 0px rgba(0, 0, 0, 0.2);
  top: 0;
}

.header-cta-banner.affix .banner-title {
  font-size: 1.125rem;
  font-weight: normal;
}

.header-cta-banner.affix .banner-phone {
  font-size: 1.25rem;
  font-weight: bold;
}

.header-cta-banner.affix .cta-banner__telgroup {
  margin: 0 auto !important;
}

.header-cta-banner__phone {
  font-size: 1.875rem;
  font-weight: bold;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 1em;
  margin-right: 1em;
  flex-wrap: nowrap;
}

@media only screen and (max-width: 47.875em) {
  .header-cta-banner__phone {
    margin-right: 0;
    font-size: 1.125rem;
  }
}

@media only screen and (max-width: 23.44em) {
  .header-cta-banner__phone {
    margin-left: 10px;
  }
}

.header-cta-banner__phone a {
  text-decoration: none;
}

.header-cta-banner__phone a:hover {
  text-decoration: inherit;
}

.agent {
  position: relative;
  padding: 0.75em 1em;
  height: 36px;
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 47.875em) {
  .agent {
    padding: 0.75em;
  }
}

.agent:after, .agent:before {
  left: 100%;
  top: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.agent:after {
  margin-top: -18px;
  border-width: 18px;
}

.agent-icon {
  margin-right: 1em;
  width: 50px;
  display: inline-block;
}

.agent-icon img {
  width: 100%;
  height: auto;
}

.agent-text {
  display: inline-block;
  text-transform: lowercase;
  margin-bottom: 0;
}

@media only screen and (max-width: 47.875em) {
  .agent-text {
    font-size: 12px;
  }
}

