//
// Global Variables
//

//
// TODO
//
// => Create variables out of elements from client provided brand identity style guides
$defaultFont: Helvetica, Arial, sans-serif;

// Base colors.
$black: #000 !default;
$white: #FFF !default;
$off-white: #eee;
$node-content-background: #f5f9fa;
$border-grey: #e3e4e4;
$medium-grey: #9f9c9d;
$darkest-grey: #5e5e5e;

// Branding.
$brand-img-size:    210px;
$footer-logo-size:  162px;

// Very human easing settings.
$ease-out-expo: cubic-bezier(0.190, 1.000, 0.220, 1.000);
$ease-primary:  $ease-out-expo;

$ease-time-default:   300ms; // Pretty natural feeling ease value.

// Navbar specific variables.
$navbar-height:   80px;
$navbar-bg:       #FFF;

// Default font size;
$font-size-base:    16px;
$font-size-lg: 		32px;
$font-size-md:      20px;
$font-size-sm:      14px;
$font-size-xs:      12px;
$font-size-xss:     11px;

// Navbar toggle size.
$navbar-toggle-size:  22px;

// Breakpoint sizes.
$screen-xxs:       23.44em; // 375px.
$screen-xxs-min:   $screen-xxs; // Min 375px.

$screen-xs:       30em; // 480px.
$screen-xs-min:   $screen-xs; // Min 480px.

// $screen-sm:       48em; // 768px.
$screen-sm:   		47.875em; // 766px
$screen-sm-min:   	$screen-sm; // Min 766px.

$screen-md:       	62em; // 992px.
$screen-md-min:   	$screen-md; // Min 992px.

$screen-lg:       	75em; // 1200px.
$screen-lg-min:   	$screen-lg; // Min 1200px.

$screen-lg-w:		87.5em; // 1400px.
$screen-lg-w-min:	$screen-lg-w; // Min 1400px.

$screen-xl:       	100em; // 1600px.
$screen-xl-min:   	$screen-xl; // Min 1600px.

// Padding Values.
$spacing-base:  	1em; // 16px.
$spacing-xxs: 		($spacing-base / 2); // 8px.
$spacing-xs:  		0.75em; // 12px.
$spacing-sm:  		1.5em; //24px.
$spacing-md:  		2em; // 32px.
$spacing-lg:  		2.5em; // 40px.
$spacing-xl:  		4.125em; // 66px.
$spacing-bs:  		15px; // Bootstrap default.

$nb-nav-padding-y:  $spacing-base;
$nb-nav-padding-x:  ($spacing-base / 2);

// Navbar Mobile + Tablet
$navbar-link-padding-m:   $spacing-lg;

$section-padding-lg:  $spacing-md;
$footer-section-padding:  2.5em;

// Filters.
$filter-padding:            0 $spacing-lg;
$filter-header-padding:     $spacing-xs 0;
$filter-footer-padding:     $spacing-xs 0 $spacing-sm;

$filter-height: 			19em; //304px
$filter-content-width:      80%;
$filter-box-shadow:         0 1px 0 0 rgba($black,0.15);
$filter-border:             1px solid rgba($black,0.03);

$filter-price-base:         50px;
$filter-price-sub:          32px;
$filter-sub-position:       -0.2em;
$filter-label-inline-pos:   1.875em; //30px

$filter-icon-size:          2.625em; //42px.
$filter-icon-spacing:       ($spacing-xs / 2);

$filter-featured-spacing: 	0.5em 0em 0.5em 1em;

// Buttons.
$btn-padding-default: 		($spacing-xs / 2) $spacing-md;
$btn-border-radius:         1.875em;

// Tables.
$table-td-height: 			6.875em;

// Comparison
$table-feature-width: 		31%;

// Navs.
$nav-list-spacing: 			0.625em 0.1875em; // 10px Y - 3px X

// Borders.
$border-width-base: 		2px;

// Agent component
$agent-icon-size: 			50px;

// Heros.
$hero-height-xs:			28em; //448px (xs-phones)
$hero-height-sm:			33em; //560px (s-phones)
$hero-height-md:			20em; //320px
$hero-height-lg:			31.25em; //500px

$hero-title-height: 		3.5em; //56px

// Featured Products.
$featured-product-width: 	21.625em; //350px


// @TODO - Post release of Allconnect, consider cleaning the media query & its variables
// Mobile Devices
$screen-mobile-min: 19.375em; // 310px
$screen-mobile-max: 46.813em; // 749px

// Tablet Devices
$screen-tablet-min: 46.875em; // 750px
$screen-tablet-max: 63.125em; // 1010px

// Desktop & Laptop Devices
$screen-desktop-min: 63.188em; // 1011px
$screen-desktop-max: 160.000em; // 2560px - @TODO remove this breakpoint

